.dichvu-thietke
    width: 100%
    height: 400px
    position: relative
    padding-top: 130px
    .breadcumb
        position: absolute
        bottom: 0px
        left: 0px
        width: 100%
        padding: 10px 0
        background: #565e67
        opacity: 0.9
        ul 
            padding: 0
            list-style: none
            li 
                display: inline-block
                margin-right: 20px     
                a 
                    color: #ebebeb
            li:nth-child(2)
                position: relative
                &:before
                    position: absolute
                    content: ''
                    display: block
                    bottom: 3px
                    left: -13px
                    width: 2px
                    height: 15px
                    background: #ebebeb
            li:nth-child(3)
                position: relative
                &:before
                    position: absolute
                    content: ''
                    display: block
                    bottom: 3px
                    left: -13px
                    width: 2px
                    height: 15px
                    background: #ebebeb